import {getUserAreas} from "../../api/user";

export default {
  namespaced: true,
  name: 'org',
  state: {
    list: []
  },
  mutations: {
    saveList(state, list) {
      state.list = list
    }
  },
  actions: {
    async fetchList({state, commit}) {

      const  res = await getUserAreas()
      commit('saveList', (res.data || []));
      return res
    }
  }
}



