<template>
  <div class="system-logo-host"></div>
</template>

<script>
  export default {
    name: "system-logo-host",
    computed: {
      globalConfig() {
        return this.$store.state.app.globalConfig;
      }
    },
    watch: {
      globalConfig(v) {
        if (v) {
          if (v.browserLogo) {
            // window.$('header')

            var link = document.querySelector("link[rel*='icon']") || document.createElement('link');
            link.type = 'image/x-icon';
            link.rel = 'shortcut icon';
            link.href = v.browserLogo;
            document.getElementsByTagName('head')[0].appendChild(link);


          }
        }
      }
    },
    mounted() {
      this.$store.dispatch('refreshGlobalConfig');
    }
  }
</script>

<style scoped>

</style>



