import axios from '@/libs/api.request'

export const getUserP = ({orgId}) => {
  const data = {
    orgId
  };
  return axios.request({
    url: '/userinfo/selectByOrgId',
    params:data,
    method: 'get'
  })
};
/**
 * 编辑个人信息
 * @param userName 用户名
 * @param fullName 全名
 * @param phoneNumber 手机号
 * @returns {*}
 */
export const EditBasic = ({phoneNumber, userName, fullName}) => {
  const data = {
    phoneNumber,
    userName,
    fullName,
  };
  return axios.request({
    url: '/userinfo/editBasic',
    data,
    method: 'post'
  })
};

/**
 * 修改密码
 * @param sourcePasswd 原密码
 * @param newPasswd 新密码
 * @param verifyPasswd 确认密码
 * @returns {*}
 */
export const EidtPasswd = ({sourcePasswd, newPasswd, verifyPasswd}) => {
  const data = {
    sourcePasswd,
    newPasswd,
    verifyPasswd,
  };
  return axios.request({
    url: '/userinfo/modifyPasswd',
    data,
    method: 'post'
  })
};

/**
 * 用户列表
 * @param name
 * @param roleName
 * @param pageNumber 分页码
 * @param pageSize 分页数
 * @returns {*}
 */
export const getUserList = ({pageNumber = 1, pageSize = 10, name,roleName}) => {
  // console.log(`getUserList  ${orgId} `)
  const data = {
    name,
    roleName,
    pageNumber,
    pageSize
  };
  return axios.request({
    url: '/userinfo/listUser',
    params: data,
    method: 'get'
  })
};
/**
 * 用户列表 (包含自己)
 * @param name
 * @param roleName
 * @param pageNumber 分页码
 * @param pageSize 分页数
 * @returns {*}
 */
export const getUserListAll = ({pageNumber = 1, pageSize = 10, name,roleName}) => {
  // console.log(`getUserList  ${orgId} `)
  const data = {
    name,
    roleName,
    pageNumber,
    pageSize
  };
  return axios.request({
    url: '/userinfo/listUserAllData',
    params: data,
    method: 'get'
  })
};

export const getUserInfo =({id})=>{
  const data = {
    id
  };
  return axios.request({
    url: '/userinfo/'+id,
    params: data,
    method: 'get'
  })
};

/**
 * 添加用户
 * @param userName 用户名
 * @param fullName 全名
 * @param passwd 密码
 * @param phoneNumber 手机号
 * @param orgId 区域id
 * @param roleId 角色id
 * @param allData 全部数据
 * @param state 状态 1-使用 0-删除 2-冻结
 * @returns {*}
 */
export const addUser = ({phoneNumber, userName, fullName, passwd, orgId, allData,state, roleId}) => {
  const data = {
    phoneNumber,
    passwd,
    userName,
    orgId,
    state,
    fullName,
    roleId,
    allData
  };
  return axios.request({
    url: '/userinfo/addUser',
    data,
    method: 'post'
  })
};

/**
 *
 * @param userId
 * @returns {*}
 */
export const deleteUser = ({userId}) => {
  const data = {
    userId
  };
  return axios.request({
    url: '/userinfo/delUser',
    data,
    method: 'post'
  })
};

/**
 * 编辑用户
 * @param userId  用户id
 * @param userName 用户名
 * @param fullName 全名
 * @param passwd 密码
 * @param phoneNumber 手机号
 * @param orgId 区域id
 * @param roleId 角色id
 * @param allData 全部数据
 * @param state 状态 1-使用 0-删除 2-冻结
 * @returns {*}
 */
export const editUser = ({state,userId,phoneNumber, userName, fullName, passwd, orgId, allData, roleId}) => {
  const data = {
    state,
    userId,
    phoneNumber,
    passwd,
    userName,
    orgId,
    fullName,
    roleId,
    allData
  };
  return axios.request({
    url: '/userinfo/editUser',
    data,
    method: 'post'
  })
};

/**
 *
 * @returns {*}
 */
export const getUserRoles = () => {
  const data = {};
  return axios.request({
    url: '/user/roles',
    data,
    method: 'get'
  })
};


/**
 *  组织区域列表
 * @returns {*}
 */
export const getUserAreas = () => {
  return axios.request({
    url: '/userinfo/orgUsers',
    method: 'get',
  })
};
/**
 *    组织新增
 * @param parentId 父id
 * @param orgName 组织名
 * @returns {*}
 */
export const addOrg = ({parentId, orgName}) => {
  const data = {
    parentId,
    orgName
  };
  return axios.request({
    url: '/org/add',
    data,
    method: 'post'
  })
};
/**
 * 组织编辑
 * @param id 组织id
 * @param parentId 父id
 * @param orgName 组织名
 * @returns {*}
 */
export const editOrg = ({orgId, parentId, orgName}) => {
  const data = {
    orgId,
    parentId,
    orgName
  };
  return axios.request({
    url: '/org/edit',
    data,
    method: 'post'
  })
};
/**
 * 组织删除
 * @param id 组织id
 * @returns {*}
 */
export const deleteOrg = ({orgId}) => {
  const data = {
    orgId
  };
  return axios.request({
    url: '/org/delete',
    data,
    method: 'post'
  })
};

/**
 * 区域用户树
 * @returns {*}
 */
export const getAreaUsers = () => {

  return axios.request({
    url: 'userinfo/orgUsers',
    method: 'get'
  })
};

/**
 * 获取操作人
 * @returns {*}
 */
export const getWoekerUsers = ({orgId,roleType}) => {
  let data = {
    orgId,roleType
  }
  return axios.request({
    url: '/userinfo/listUserByRoleType',
    params: data,
    method: 'get'
  })
};



