import buildConfigs from '../../config'

let buildConfig
if (process.env.NODE_ENV !== 'production') {
  buildConfig = buildConfigs.dev
} else {
  buildConfig = buildConfigs.build
}

export default {
  /**
   * @description token在Cookie中存储的天数，默认1天
   */
  cookieExpires: 1,

  /**
   * api 的根路径
   */
  baseUrl: '/device-ai',

  downLoadUrl: '/device-batch-ai',

  suanpanUrl: '/suanpan',

  faultUrl: '/fault',

  dpfUrl: '/dpf',

  decisionUrl: '/decision',
  /**
   * 是否使用模拟数据
   */
  useMock: false,

  buildConfig
}



