// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'

import vuescroll from 'vuescroll'

import 'vuescroll/dist/vuescroll.css'
// import 'lib-flexible/flexible.js'
import '../static/js/flexible.js'
import ViewUI from 'view-design'
import 'view-design/dist/styles/iview.css'

import App from './App'
import router from './router'
import store from './store'
import importDirective from './directives'
import config from './config'
import TreeTable from 'tree-table-vue'
import '../static/vendor/animate.min.css'
import './style/index.less'
import '../static/icons/iconfont.css'
import '../static/icons/digital.css'
import * as flvjs from '../static/js/flv.min'

import { getFlexibleSize, hasAccess } from '@/libs/util'
import scroll from 'vue-seamless-scroll'
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
import VideoPlayer from 'vue-video-player'

import I18n, { i18nOptions } from '@/libs/i18n'

import * as echarts from 'echarts'
// import {
//   LineChart
// } from 'echarts/charts';
// import {
//   TitleComponent,
//   TooltipComponent,
//   GridComponent
// } from 'echarts/components'
// import {
//   CanvasRenderer
// } from 'echarts/renderers';
// echarts.use(
//   [TitleComponent, TooltipComponent, GridComponent, BarChart, CanvasRenderer]
// );

Vue.use(I18n)
Vue.use(flvjs)
Vue.use(TreeTable)

Vue.use(scroll)
Vue.use(preview)
// ivew
Vue.use(ViewUI, {
  i18n: function (path, options) {
    let value = i18nOptions.t(path, options)
    if (value !== null && value !== undefined) {
      return value
    }
    return ''
  }
})
Vue.use(vuescroll)

/* eslint-disable */ //调试模式下启用模拟数据
if (config.useMock && process.env.NODE_ENV !== 'production') require('@/mock');


//设置为 false 以阻止 vue 在启动时生成生产提示。
Vue.config.productionTip = false;
//eventBus
Vue.prototype.$eventHub = Vue.prototype.$eventHub || new Vue();

Vue.prototype.$hasAccess = function(needAuth) {
    return hasAccess(this.$store.state.user.modules || [], (typeof needAuth === 'string' ? [needAuth] : needAuth))
};

Vue.prototype.$flexible = getFlexibleSize

/**
 * @description 全局注册应用配置
 */
Vue.prototype.$config = config
    /**
     * 挂载echarts
     */
Vue.prototype.$echarts = echarts
    /**
     * 注册指令
     */
importDirective(Vue)


require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
require('videojs-contrib-hls');
Vue.use(VideoPlayer);

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n: i18nOptions,
    components: { App },
    template: '<App/>'
})


