import {getRouteConfig, getUserAuth, getUserInfo, guestLogin, login, logout} from '@/api/auth'
import {getToken, setToken} from '@/libs/util'
import {destroy as socketDestroy, init as socketInit} from '@/libs/socket'

export default {
  state: {
    userName: '',
    userId: '',
    avator: '',
    fullName: '',
    phoneNumber: '',
    token: getToken(),
    modules: [],
    routes: {},
    loaded: false
  },
  mutations: {
    setInfo (state, info) {
      // console.log("setInfo")
      // console.log(info)
      Object.assign(state, {
        userName: '',
        userId: '',
        avator: '',
        fullName: '',
        phoneNumber: '',
        token: '',
        modules: []
      }, info)
    },
    setModules (state, modules = []) {
      state.modules = modules
    },
    setUserRoute (state, routes = {}) {
      let menus = routes.menus
      let menuMap = {}

      function getMenuMap (menu, pMenu) {
        let key = menu.route || menu.title
        menuMap[key] = {
          menu: menu,
          parent: pMenu
        }

        if (menu.params && typeof menu.params === 'string') {
          try {
            // 解析参数，统一放在params里面
            let params = JSON.parse(menu.params)
            menu.params = params.params
            menu.query = params.query
          } catch (e) {
            menu.params = null
            menu.query = null
          }
        }

        if (menu.subMenus) {
          menu.subMenus.forEach((smenu) => {
            getMenuMap(smenu, menu)
          })
        }
      }

      menus.forEach((menu) => {
        getMenuMap(menu)
      })

      state.routes = {
        ...routes,
        menuMap
      }
    }
  },
  actions: {
    // 登录
    temLogin ({commit}) {
      return new Promise((resolve, reject) => {
        guestLogin({}).then(res => {
          const data = res.data
          let token = data.token
          commit('setInfo', data)
          setToken(token)
          Promise.all([getRouteConfig(), getUserAuth()])
            .then((resList) => {
              commit('setModules', resList[1].data)
              commit('setUserRoute', resList[0].data)
              resolve()
            }).catch((err) => {
              reject(err)
            })
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleLogin ({commit}, {userName, password, validateToken, validateCode}) {
      console.log('handleLogin')
      userName = userName.trim()
      return new Promise((resolve, reject) => {
        login({
          userName,
          password,
          validateToken,
          validateCode
        }).then(res => {
          console.log(res)
          const data = res.data
          let token = data.token
          commit('setInfo', data)
          setToken(token)
          socketInit()
          // socketInit();
          // Promise.all([getRouteConfig()])
          //   .then((resList) => {
          //     commit('setUserRoute', resList[0].data);
          //     resolve()
          //   }).catch((err) => {
          //   reject(err)
          // });
          // console.warn("暂时取消获取权限")
          Promise.all([getRouteConfig(), getUserAuth()])
            .then((resList) => {
              commit('setModules', resList[1].data)
              commit('setUserRoute', resList[0].data)

              resolve(resList)
            }).catch((err) => {
              reject(res)
            })
        }).catch(err => {
          reject(res)
        })
      })
    },
    // 退出登录
    handleLogOut ({state, commit}) {
      return new Promise((resolve, reject) => {
        logout().then(() => {
          commit('setInfo', {})
          setToken('')
          socketDestroy()
          resolve()
        }).catch(err => {
          reject(err)
        })
      })
    },

    getUserInfo ({state, commit}, data) {
      return new Promise((resolve, reject) => {
        let force = data && data.force
        if (!force && state.loaded) {
          resolve(state)
          return
        }

        getUserInfo().then(res => {
          const data = res.data
          commit('setInfo', {
            ...data,
            loaded: true
          })
          socketInit()
          console.warn('暂时取消获取权限')
          /* Promise.all([getRouteConfig()])
             .then((resList) => {
               commit('setUserRoute', resList[0].data);
               resolve(state)
             }).catch((err) => {
             reject(err)
           }); */
          Promise.all([getRouteConfig(), getUserAuth()])
            .then((resList) => {
              commit('setModules', resList[1].data)
              commit('setUserRoute', resList[0].data)
              resolve(state)
            }).catch((err) => {
              reject(err)
            })
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
}



