import {getGlobalConfig} from '../../api/global'

import {init as socketInit} from '@/libs/socket'

export default {
  state: {
    globalConfig:{},
    cmdList:[],
    cmdTip:[],
    status:true,
  },
  getters: {},
  mutations: {
    setGlobalConfig(state, globalConfig) {
      state.globalConfig = {
        ...globalConfig||{}
      };
    },
    setCmdList(state, cmdList){
      state.cmdList = [...cmdList]
    },
    setState(state, status){
      state.status = status
    },
    setCmdTip(state, cmdTip){
      state.cmdTip = cmdTip
    }
  }
  ,
  actions: {
    refreshGlobalConfig({state, commit}){
      // commit('setGlobalConfig', isBigScreen);
      getGlobalConfig().then((res)=>{
        commit('setGlobalConfig', res.data);
        socketInit(res.data);
      })
    },
    refreshScreenSize({state, commit}) {
      window.lib.flexible.setCustomRate(1920 / Math.max(1024, window.innerWidth))
    }
  }
}



