import axios from '@/libs/api.request'

export const getGlobalConfig = () => {
  return new Promise((resolve, reject) => {
    Promise.all([
      axios.request({
        url: '/logo/logo',
        params: {},
        method: 'get'
      }),
      axios.request({
        url: '/config/getConfig',
        params: {},
        method: 'get'
      })
    ]).then((resList) => {
      let logo = resList[0] && resList[0].data || {}
      let cfg = resList[1] && resList[1].data || {}
      resolve({
        data: {
          ...logo,
          ...cfg
        }
      })
    }, (err) => {
      reject(err)
    })
  })
}



