<template>
  <div id="app">
    <transition :enter-active-class="routerTransitionClass.enter"
                :leave-active-class="routerTransitionClass.leave">
      <router-view style="position: absolute;width: 100%;" />
    </transition>
    <SystemLogoHost></SystemLogoHost>
  </div>

</template>

<script>
  import './style/app.less'
  import SystemLogoHost from '@/components/system-logo-host'

  import RouterTransition from '@/mixins/router-transition'

  export default {
    name: 'App',
    mixins: [RouterTransition],
    components:{
      SystemLogoHost
    },
    methods: {
      refreshScreenSize() {
        this.$store.dispatch('refreshScreenSize');
        this.$eventHub.$emit('sl-main-size-change');
      }
    },
    mounted() {
      let timer;
      window.$(window).on('resize', () => {
        clearTimeout(timer);
        timer = setTimeout(this.refreshScreenSize, 300)
      });

      this.refreshScreenSize()
    },
    destroyed() {

    }
  }
</script>

<style>
@import "../static/font/font.css";
</style>



