import {getRouteRelation, RoutesRelationName} from '@/libs/util'

const transitionClasses = {
  fromLeft: {
    // enter: 'animated slideInLeft',
    // leave: 'animated slideOutRight'
    // enter: 'animated fadeIn',
    // leave: 'animated fadeOut'
  },
  fromRight: {
    // enter: 'animated slideInLeft',
    // leave: 'animated fadeOut'
    // leave: 'animated slideOutLeft'
    // enter: 'animated fadeIn',
    // leave: 'animated fadeOut'
  },
  none: {
    // enter: 'animated slideInLeft',
    // leave: 'animated fadeOut'
  }
}

const ClsDataName = 'routerTransitionClass'

/**
 * 路由切换动态效果mixin
 * @type {{data(): *, beforeRouteUpdate(*=, *=, *): void}}
 */
let RouterTransition = {
  data () {
    return {
      [ClsDataName]: transitionClasses.fromLeft
    }
  },
  beforeRouteUpdate (to, from, next) {
    // 这行代码就是只是为了在webstorm里面打开组件编辑的时候，不提示波浪号
    this.routerTransitionClass = {}
    let relation = getRouteRelation(to, from)
    // console.log(relation);
    switch (relation) {
      case RoutesRelationName.Parent:
        this[ClsDataName] = transitionClasses.fromRight
        break
      case RoutesRelationName.Child:
        this[ClsDataName] = transitionClasses.fromLeft
        break
      case RoutesRelationName.Self:
        this[ClsDataName] = transitionClasses.none
        break
      case RoutesRelationName.NoRelation:
        this[ClsDataName] = transitionClasses.fromRight
        break
    }
    this.$nextTick(() => {
      next()
    })
  }

}

export default RouterTransition



