import axios from '@/libs/api.request'

export const login = ({userName, password, validateToken, validateCode}) => {
  const data = {
    userName,
    passwd: password,
    validateToken,
    validateCode
  }

  return axios.request({
    url: 'login/login',
    data,
    method: 'post'
  })
}

export const guestLogin = ({}) => {
  const data = {}
  return axios.request({
    url: '/userinfo/getGusetToken',
    data,
    method: 'get'
  })
}

export const refreshVerifyCode = ({}) => {
  const data = {}
  return axios.request({
    url: '/verifycode/refreshVerifyCode',
    data,
    method: 'get'
  })
}

export const getUserAuth = () => {
  return axios.request({
    url: '/userinfo/modules',
    params: {},
    method: 'get'
  })
}

export const getUserInfo = () => {
  return axios.request({
    url: '/userinfo/getUserInfo',
    params: {},
    method: 'get'
  })
}

export const logout = (token) => {

  return axios.request({
    url: 'login/logout',
    method: 'get'
  })
}

export const getRouteConfig = () => {
  return axios.request({
    url: 'module/listUserMenu',
    method: 'get'
  })
}

export const getSuperAllModules = () => {
  return axios.request({
    url: 'module/getSuperModules',
    method: 'get'
  })
}

export const getEntModules = ({
                                entId
                              }) => {
  return axios.request({
    url: 'module/getEntModules',
    params: {
      entId
    },
    method: 'get'
  })
}
export const saveEntModules = ({
                                    entId,
                                    modules
                                  }) => {
  return axios.request({
    url: 'module/saveEntModules',
    data: {
      entId,
      moduleIds:modules
    },
    method: 'post'
  })
}





