import axios from 'axios'
import Vue from 'vue'
import router from '@/router'

import { getLang, getToken, setToken } from '@/libs/util'

const CancelToken = axios.CancelToken
axios.defaults.timeout = 150000
// import { Spin } from 'iview'
class HttpRequest extends Vue {
  constructor (baseUrl) {
    super()
    this.baseUrl = baseUrl
    this.queue = {}
  }

  getInsideConfig () {
    const config = {
      baseURL: this.baseUrl,
      headers: {
        token: getToken(),
        'Accept-Language': getLang()
      }
    }
    return config
  }

  distroy (url) {
    delete this.queue[url]
  }

  interceptors (instance, url) {
    // 请求拦截
    instance.interceptors.request.use(config => {
      return config
    }, error => {
      return Promise.reject(error)
    })
    // 响应拦截
    instance.interceptors.response.use(res => {
      this.distroy(url)

      const { status, config } = res
      let data = res.data
      if (status >= 200 && status < 400) {
        if (config.responseType && ['ArrayBuffer', 'arraybuffer'].indexOf(config.responseType) !== -1) {
          return res
        }

        // IE9 无data返回处理
        if (data === undefined) {
          data = JSON.parse(res.request.responseText)
        }

        // session 失效
        if (data.code === 201) {
          setToken('')
          // this.$Message.warning('token已失效')
          router.replace({
            name: 'login'
            // query: {
            //   from: location.href
            // }
          })
          return
        }
        // 正常返回
        if (data.code === 200) {
          return {
            ...data,
            data: data.data || []
          }
        } else {
          // 业务错误
          if (data.code === 1000) {
            return {
              ...data,
              data: data.data || []
            }
          } else {
            // 其他错误
            return {
              ...data,
              data: data.data || []
            }
          }
        }
      } else {
        return Promise.reject(res)
      }
    }, error => {
      this.distroy(url)
      return Promise.reject(error)
    })
  }

  request (options) {
    if (options.useLatest && this.queue[options.url]) {
      return this.queue[options.url]
    }

    const instance = axios.create()
    const source = CancelToken.source()

    options = Object.assign(this.getInsideConfig(), options, {
      _source: source,
      cancelToken: source.token
    })

    this.interceptors(instance, options.url)
    let dtd = Object.assign(instance(options), {
      // 取消任务
      doCancel: (msg) => {
        source.cancel(msg)
      }
    })
    this.queue[options.url] = dtd

    return dtd
  }
}

export default HttpRequest



