import Vue from 'vue'
import Vuex from 'vuex'
import app from './module/app'
import user from './module/user'
import org from './module/org'

Vue.use(Vuex);

export default new Vuex.Store({
  state:{},
  mutations:{},
  actions:{},
  modules:{
    app,
    org,
    user
  }
})



