import VueI18n from 'vue-i18n'
import en from 'iview/dist/locale/en-US'
import zh from 'iview/dist/locale/zh-CN'
import en_all from '../i18n/en-US'
import zh_all from '../i18n/zh-CN'
import config from '../config'
import {LANG_ENUM} from './constants'

import {getLang, setLang} from './util'

export let i18nOptions;


export default {
  install(Vue, options) {

    Vue.use(VueI18n);

    // 这行代码不能去掉，否则iview里面多语言切换不能生效
    Vue.locale = () => {
    };
    const locale = getLang();

    // console.log('en_all', en_all)

    const messages = {
      [LANG_ENUM.EN_US]: Object.assign(en_all, en),
      [LANG_ENUM.ZH_CN]: Object.assign(zh_all, zh)
    };

    i18nOptions = new VueI18n({
      locale,  // set locale
      messages  // set locale messages
    })
    setLang(i18nOptions.locale)
    // 自动存储
    i18nOptions.vm.$watch('locale', (v) => {
      setLang(v)
    })



    // 自动映射模板
    Vue.prototype.$t_source = function (key) {
      return  this.$t.apply(this,Array.prototype.slice.call(arguments).concat([{
        // 读取编译配置文件中的路径来组装静态目录
        static: `${config.buildConfig.assetsPublicPath}${config.buildConfig.assetsSubDirectory}`
      }]))
    }

    // 可以在组件定义的参数里面加上 i18n_prefix 来设置多语言字符串key的统一前缀
    // 短名称
    Vue.prototype.$t_p = function (key) {
      const prefix = this.$options.i18n_prefix ? `${this.$options.i18n_prefix}.` : '';
      return  this.$t.apply(this,[`${prefix}${key}`].concat(Array.prototype.slice.call(arguments,1)))
    }

    // 自动映射模板 - 短名称
    Vue.prototype.$t_p_source = function (key, local, values) {
      return  this.$t_p.apply(this,Array.prototype.slice.call(arguments).concat([{
        // 读取编译配置文件中的路径来组装静态目录
        static: `${config.buildConfig.assetsPublicPath}${config.buildConfig.assetsSubDirectory}`
      }]))
    }

  }
}



