import Cookies from 'js-cookie'

import config from '../config'
import {LANG_ENUM} from './constants'
/* ==================================================================== */

export const TOKEN_KEY = 'token'
export const LANG_KEY = 'lang'

export const setToken = (token) => {
  Cookies.set(TOKEN_KEY, token, {expires: config.cookieExpires || 1})
}

export const getToken = () => {
  // if(1){
  //   return "2491E09CC45046E2AA0D03785E48D371";
  // }
  const token = Cookies.get(TOKEN_KEY)
  if (token) return token
  else return ''
}
export const paramUrl = () => {
  // return "http://10.88.36.123"
  return window.location.origin
}

const checkLang = (lang) => {
  if (Object.values(LANG_ENUM).includes(lang)) {
    return lang
  }
}

export const setLang = (lang) => {
  if (checkLang(lang)) {
    localStorage.setItem(LANG_KEY, lang)
  }
}

export const getLang = () => {
  return checkLang(localStorage.getItem(LANG_KEY)) || LANG_ENUM.ZH_CN
}

/* ==================================================================== */

export const hasAccess = (userAccess = [], needAccess) => {
  // 超管权限
  if (userAccess.includes('superman')) {
    return true
  }
  for (let i = 0; i < needAccess.length; i++) {
    if (!userAccess.includes(needAccess[i])) {
      return false
    }
  }
  return true
}

/**
 * 权鉴
 * @param {*} toRoute 将要跳转的路由
 * @param {*} modules 用户拥有的权限
 * @description 用户是否可跳转到该页
 */
export const canTurnTo = (toRoute, modules) => {
  return hasAccess(modules, toRoute.meta.auth || [])
  // return true
}

/* ==================================================================== */

export const RoutesRelationName = {
  'Parent': 'Parent',
  'Child': 'Child',
  'NoRelation': 'NoRelation',
  'Self': 'Self'
}

/**
 * 获取两个路由之间的关系
 * @param toRoute 目标路由
 * @param fromRoute 起始路由
 * @returns {string}
 */
export const getRouteRelation = (toRoute, fromRoute) => {
  let relation = RoutesRelationName.NoRelation
  // console.log(toRoute.path)
  // console.log(fromRoute.path)
  if (toRoute.name === fromRoute.name) {
    relation = RoutesRelationName.Self
  } else if (toRoute.path.indexOf(fromRoute.path) === 0) {
    relation = RoutesRelationName.Parent
  } else if (fromRoute.path.indexOf(toRoute.path) === 0) {
    relation = RoutesRelationName.Child
  }
  return relation
}

/* ==================================================================== */

/**
 * @param {String} url
 * @description 从URL中解析参数
 */
export const getParams = url => {
  const keyValueArr = url.split('?')[1].split('&')
  let paramObj = {}
  keyValueArr.forEach(item => {
    const keyValue = item.split('=')
    paramObj[keyValue[0]] = keyValue[1]
  })
  return paramObj
}

/**
 * @param {Number} times 回调函数需要执行的次数
 * @param {Function} callback 回调函数
 */
export const doCustomTimes = (times, callback) => {
  let i = -1
  while (++i < times) {
    callback(i)
  }
}

/* ==================================================================== */

/**
 * 树形数据转换
 * @param treeData 原始数据
 * @param transformFunc 每一项转换方法
 * @param childrenKey 子项属性名
 * @param targetChildKey 转换后的子项转换名
 * @param level 当前层级，默认可以不传
 * @returns {*}
 */
export const treeTransform = (treeData, transformFunc, childrenKey = 'children', targetChildKey, level = 0) => {
  // console.log('treeTransform', treeData)
  if (treeData instanceof Array) {
    return treeData.map((d) => treeTransform(d, transformFunc, childrenKey, targetChildKey, ++level))
  } else {
    targetChildKey = targetChildKey || childrenKey
    let result = {
      ...treeData
    }
    if (treeData[childrenKey]) {
      result[targetChildKey] = treeTransform(treeData[childrenKey], transformFunc, childrenKey, targetChildKey, ++level)
    }

    result = {
      ...result,
      ...transformFunc(result, level)
    }
    return result
  }
}

/* ==================================================================== */

/**
 * 根据设计稿像素获取实际页面像素，主要用于iview组件在模板中配置尺寸的时候，使用
 * @param size，设计稿像素值
 * @returns {number}
 */
export const getFlexibleSize = (size) => {
  return size / window.lib.flexible.getCustomRate()
}

/* ==================================================================== */
export const BIND_PREFIX = 'xl-bind-'

export const cellToInfo = (cell, editor) => {
  let value = editor.graph.getModel().getValue(cell)
  let detail = {}
  Array.from(value && value.attributes || []).forEach((attr) => {
    if (attr.name.indexOf(BIND_PREFIX) === 0) {
      let name = attr.name.slice(BIND_PREFIX.length)
      let val
      try {
        val = JSON.parse(attr.value)
      } catch (e) {
        val = attr.value
      }

      if (typeof val !== 'object') {
        val = val + ''
      }
      detail[name] = val
    }
  })

  return {
    cell,
    detail
  }
}

/**
 *  绑定信息设置到图像元素
 * @param info
 * @param editor
 * @param cell
 */
export const infoToCellValue = (info, editor, cell) => {
  let value = editor.graph.getModel().getValue(cell)
  // Converts the value to an XML node

  if (!mxUtils.isNode(value)) {
    let doc = mxUtils.createXmlDocument()
    let obj = doc.createElement('object')
    value = obj
  } else {
    value = value.cloneNode(true)
  }

  for (let name in info) {
    if (typeof info[name] === 'object') {
      value.setAttribute(BIND_PREFIX + name, JSON.stringify(info[name]))
    } else {
      value.setAttribute(BIND_PREFIX + name, info[name])
    }
  }

  value.setAttribute('tooltip', '')
  editor.graph.getModel().setValue(cell, value)
}

let
  changeStyle = function (cell, styleObj) {
    let style = {}
    cell.getStyle() && cell.getStyle().split(';').forEach((item) => {
      if (item) {
        let arr = item.split('=')
        style[arr[0]] = arr[1]
      }
    })
    Object.assign(style, styleObj)
    let nStyle = ''
    for (let k in style) {
      nStyle += `${k}=${style[k]};`
    }
    cell.setStyle(nStyle)
  },
  changeFillColor = function (cell, fillColor) {
    changeStyle(cell, {
      [mxConstants.STYLE_FILLCOLOR]: fillColor
    })
  },
  changeTextColor = function (cell, fontColor) {
    changeStyle(cell, {
      [mxConstants.STYLE_FONTCOLOR]: fontColor
    })
  },
  changeImage = function (cell, image, imageWidth, imageHeight) {
    changeStyle(cell, {
      [mxConstants.STYLE_IMAGE]: image,
      // [mxConstants.STYLE_IMAGE_BACKGROUND] :image,
      // [mxConstants.STYLE_SHAPE] :mxConstants.SHAPE_LABEL,
      // [mxConstants.STYLE_SPACING] :0,
      // [mxConstants.SHADOW_OFFSET_X] :0,
      // [mxConstants.STYLE_STROKEWIDTH] :0,
      [mxConstants.STYLE_IMAGE_WIDTH]: imageWidth,
      [mxConstants.STYLE_IMAGE_HEIGHT]: imageHeight
    })
  }

/**
 * 数据映射到图像
 * @param cell 图像信息
 * @param mappings 映射信息
 * @param data 实际数据
 */
export const mappingDataToCell = (cell, mappings, data) => {
  let val = data && data.val
  if (val == null) {
    return
  }

  val = Number(val)

  for (let i = 0; i < mappings.length; i++) {
    let m = mappings[i]
    let x1 = Number(m.x1)
    let x2 = Number(m.x2)
    let match = false
    switch (m.op) {
      case '==':
        match = val === x1
        break
      case '><':
        match = val !== x1
        break
      case '>':
        match = val > x1
        break
      case '>=':
        match = val >= x1
        break
      case '<':
        match = val < x1
        break
      case '<=':
        match = val <= x1
        break
      case '>&<':
        match = val > x1 && val < x2
        break
      case '>=&<':
        match = val >= x1 && val < x2
        break
      case '>&<=':
        match = val > x1 && val <= x2
        break
      case '>=&<=':
        match = val >= x1 && val <= x2
        break
    }
    if (match) {
      switch (m.type) {
        case 'text':
          cell.setAttribute('label', m.text)
          break
        case 'textColor':
          // cell.setAttribute('label', m.color);
          changeTextColor(cell, m.textColor)
          break
        case 'bgColor':
          // cell.setAttribute('label', m.color);
          changeFillColor(cell, m.bgColor)
          break
        case 'image':
          // cell.setAttribute('label', m.image);
          // let img = new mxImage(m.image,cell.geometry.width,cell.geometry.height);
          // cell.setGeometry(img);
          changeImage(cell, m.image, cell.geometry.width, cell.geometry.height)
          break
      }
      // break
    }
  }

  // cell.setStyle(changeImage(cell.getStyle(), imageSrc))
}

/**
 * 获取图像数据中绑定数据类型
 * @param cell
 * @returns {*|string}
 */
export const getCellBindType = (cell) => {
  return cell && cell.getAttribute('xl-bind-type')
}

export const getCellBindCmd = (cell) => {
  return cell && cell.getAttribute('xl-bind-cmdType')
}

/**
 * 获取清除了绑定关系的图像xml
 * @param graphXml
 */
export const getCleanXmlFromGraph = (graphXml) => {
  let reg = `${BIND_PREFIX}[\\S]+="[\\S]*"`
  // console.log(reg);
  return graphXml.replace(new RegExp(reg, 'g'), '')
}



