import store from '@/store'

const getDefaultRoute = routeStr => {
  try {
    let defaultPage = JSON.parse(store.state.user.routes.defaultPage || '{}')
    let params = defaultPage.params || {}
    let query = defaultPage.query || {}
    return {
      name: defaultPage.name,
      params: params,
      query: query
    }
  } catch (e) {
    return routeStr
  }
}

